<template>
  <div id="app">

<section class="hero is-info bsa">
  <div class="container">
    <div class="columns is-vcentered">
      <div class="column is-12">
        <p class="title">{{ msg }}<strong></strong></p>
        <p class="subtitle"></p>
      </div>
    </div>
  </div>
</section>

<section class="hero bsa">
  <div class="container">
    <div class="columns is-vcentered">
      <div class="column is-12">

    <el-row :gutter="20">
    <el-col :span="24" :offset="0">

<el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-width="120px" class="demo-ruleForm">
  <el-form-item label="メールアドレス" prop="email">
    <el-input type="email" v-model="ruleForm2.email" auto-complete="off"><template slot="prepend">メールアドレス</template></el-input>
  </el-form-item>
  <el-form-item label="パスワード" prop="pass">
    <el-input type="password" v-model="ruleForm2.pass" auto-complete="off"><template slot="prepend">&nbsp;パスワード&nbsp;</template></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="submitForm('ruleForm2')">Submit</el-button>
    <el-button @click="resetForm('ruleForm2')">Reset</el-button>
  </el-form-item>

              <div class="field is-grouped">
                <div class="control has-icons-left is-expanded">
                  <el-input value="" name="EMAIL" class="is-flat required email" id="mce-EMAIL" placeholder="メールアドレス" required="" aria-required="true" type="email">
                  <span class="icon is-small is-left">
                    <i class="fa fa-envelope"></i>
                  </span></el-input>
                </div>
              </div>

</el-form>

    </el-col>
    </el-row>

      </div>
    </div>
  </div>
</section>

  </div>
</template>

<script>
import log from 'loglevel'
import firebase from 'firebase'
import Topnav from './Topnav.vue'
export default {
  name: 'login',
  components: {
    log,
    firebase,
    Topnav
  },
  data () {
    const checkAge = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('Please input the age'))
      }
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error('Please input digits'))
        } else {
          if (value < 18) {
            callback(new Error('Age must be greater than 18'))
          } else {
            callback()
          }
        }
      }, 1000)
    }
    const checkEmail = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('Please input email address'))
      }
      setTimeout(() => {
        if (value !== '') {
          log.debug('// checkEmail')
        }
        callback()
      }, 1000)
    }
    const validaePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('Please input the password'))
      } else {
        if (this.ruleForm2.checkPass !== '') {
          this.$refs.ruleForm2.validateField('checkPass')
        }
        callback()
      }
    }
    const validaePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('Please input the password again'))
      } else if (value !== this.ruleForm2.pass) {
        callback(new Error('Two inputs don\'t match!'))
      } else {
        callback()
      }
    }
    return {
      msg: 'ログイン',
      formInline: {
        user: '',
        region: ''
      },
      ruleForm2: {
        email: '',
        pass: '',
        checkPass: '',
        age: ''
      },
      rules2: {
        email: [
          // { validator: checkEmail, trigger: 'blur' }
          { required: true, message: 'Please input email address', trigger: 'blur' },
          { type: 'email', message: 'Please input correct email address', trigger: 'blur,change' }
        ],
        pass: [
          { validator: validaePass, trigger: 'blur', required: true }
        ],
        checkPass: [
          { validator: validaePass2, trigger: 'blur' }
        ],
        age: [
          { validator: checkAge, trigger: 'blur' }
        ]
      },
      firebaseConfig: {
        apiKey: "AIzaSyCnBvwzNrltP-FmTLimRJ5-D8nylh5AqVI",
        authDomain: "kourabiz.firebaseapp.com",
        databaseURL: "https://kourabiz.firebaseio.com",
        projectId: "kourabiz",
        storageBucket: "kourabiz.appspot.com",
        messagingSenderId: "989608312358"
      },
      user: null,
      token: ''
    }
  },
  created: function () {
    log.setLevel(0)
    log.debug('// created')
  },
  mounted: function () {
    log.debug('// mounted')
    this.init()
  },
  computed: {
    showSlide: function () {
      switch (this.slidestate) {
        case 1: return 1
      }
    }
  },
  methods: {
    init () {
      log.debug('// init')
      log.debug(firebase)
      firebase.initializeApp(this.firebaseConfig)
      this.fireBaseListener()
    },
    fireBaseListener () {
      log.debug('// authListener')
      let _me = this
      firebase.auth().onAuthStateChanged(function (user) {
        log.debug(user)
        if (user) {
          // User is signed in.
          log.debug('// User is signed in')
          _me.$notify({
            title: 'User is signed in',
            message: '',
            duration: 6000
          })
          _me.user = user
        } else {
          // No user is signed in.
          log.debug('// No user is signed in')
        }
      })
    },
    startHacking () {
      log.debug(this.user)

      // const user = firebase.auth().currentUser;
      if (this.user) {
        // User is signed in.
        log.debug(this.user)
        this.$notify({
          title: 'User is signed in',
          message: 'user email: ' + this.user.email,
          duration: 6000
        })
      } else {
        // No user is signed in.
        this.$notify({
          title: 'No user is signed in',
          message: '',
          duration: 6000
        })
      }

    },
    onSubmit () {
      log.debug('サブミット')
      let provider = new firebase.auth.GoogleAuthProvider()
      provider.addScope('profile')
      provider.addScope('email')

      firebase.auth().signInWithPopup(provider)
        .then(function (result) {
          log.debug(result)
          log.debug(result.user)
          log.debug(result.credential)
          log.debug(result.operationType)
          // This gives you a Google Access Token. You can use it to access the Google API.
          var token = result.credential.accessToken
          // The signed-in user info.
          var user = result.user
          // ...
        }).catch(function (error) {
          log.debug(error)
          // Handle Errors here.
          var errorCode = error.code
          var errorMessage = error.message
          // The email of the user's account used.
          var email = error.email
          // The firebase.auth.AuthCredential type that was used.
          var credential = error.credential
          // ...
        })

    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!')
          log.debug(this.ruleForm2.email)
          log.debug(this.ruleForm2.pass)

          // firebase.auth().signInWithEmailAndPassword('koura718@gmail.com', 'colac0la#!')
          firebase.auth().signInWithEmailAndPassword(this.ruleForm2.email, this.ruleForm2.pass)
            .then(function (result) {
              log.debug('// Auth success.')
              log.debug(result)
              const _email = result.email
              // The signed-in user info.
              const _name = result.displayName
              const _uid = result.uid
              const _token = result.refreshToken
              log.debug(_name)
              log.debug(_email)
              log.debug(_uid)
              log.debug(_token)

              const user = firebase.auth().currentUser

              user.sendEmailVerification()
                .then(function () {
                  // Email sent.
                  log.debug('// Email sent.')
                }, function(error) {
                  // An error happened.
                  log.debug('// An error happened.')
                })

              firebase.auth().signOut()
                .then(function() {
                  // Sign-out successful.
                  log.debug('// Sign-out successful.')
                }, function(error) {
                  // An error happened.
                  log.debug('// An error happened.')
                })
            })
            .catch(function (error) {
              // Handle Errors here.
              log.debug('// Handle Errors here.')
              log.debug(error)
              const errorCode = error.code
              const errorMessage = error.message
              // ...
            })

          const provider = new firebase.auth.GoogleAuthProvider()

        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>

<style>
body {
  font-family: Helvetica, sans-serif;
  text-align: center;
}

.el-form-item__error {
    color: #FF4949;
    font-size: 12px;
    line-height: 1;
    padding-top: 0px;
    position: absolute;
    top: 2px;
    left: 20px;
}
</style>



